<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <h2>Add New Document</h2>
      <p>Fill in the details of the document you want to add.</p>

      <form @submit.prevent="addDocument">
        <div class="form-group">
          <label for="name">Document Name:</label>
          <input type="text" id="name" v-model="newDocument.name" required />
        </div>
        <div class="form-group">
          <label for="filename">File Name:</label>
          <input type="text" id="filename" v-model="newDocument.filename" required />
        </div>
        <div class="form-group">
          <label for="creationDate">Creation Date:</label>
          <input type="date" id="creationDate" v-model="newDocument.creationDate" required />
        </div>
        <div class="form-group">
          <label for="type">Type:</label>
          <select id="type" v-model="newDocument.type" required>
            <option value="Exam">Exam</option>
            <option value="Course">Course</option>
            <option value="Document">Document</option>
          </select>
        </div>
        <div class="form-group">
          <label for="file">Upload File:</label>
          <input type="file" id="file" @change="handleFileUpload" required />
        </div>

        <div class="modal-buttons">
          <button type="submit" class="add-button">Add Document</button>
          <button @click="$emit('close')" class="close-button">Close</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newDocument: {
        name: '',
        filename: '',
        creationDate: '',
        type: '',
        file: null // Campo para almacenar el archivo
      }
    };
  },
  methods: {
    handleFileUpload(event) {
      this.newDocument.file = event.target.files[0]; // Guarda el archivo seleccionado
    },
    addDocument() {
      // Lógica para agregar el documento
      // Aquí puedes manejar la carga del archivo, enviar a la API, etc.
      console.log('Document added:', this.newDocument);
      this.$emit('close'); // Cierra el modal
    }
  }
}
</script>
<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: #ffffff;
  padding: 30px;
  border-radius: 8px;
  width: 500px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

h2 {
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input[type="text"],
input[type="date"],
input[type="file"],
select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.add-button {
  background-color: #28a745; /* Verde pasto */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
  margin-right: 10px;
}

.add-button:hover {
  background-color: #218838; /* Verde más oscuro */
}

.close-button {
  background-color: #dc3545; /* Rojo */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.close-button:hover {
  background-color: #c82333; /* Rojo más oscuro */
}
</style>
