<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <h2>Employee Conclusions</h2>
      <p>You can view, add, or edit the conclusions related to the employee.</p>
      
      <div class="conclusions-list">
        <ul>
          <li v-for="(item, index) in conclusions" :key="index" class="conclusion-item">
            <strong>{{ item.title }}</strong>: {{ item.text }}
            <div class="item-actions">
              <button class="edit-button" @click="editConclusion(index)">Edit</button>
              <button class="delete-button" @click="deleteConclusion(index)">Delete</button>
            </div>
          </li>
        </ul>
      </div>

      <div class="new-conclusion">
        <input 
          type="text" 
          v-model="newTitle" 
          placeholder="Conclusion Title" 
        />
        <textarea 
          v-model="newText" 
          placeholder="Add a new conclusion" 
          rows="3"
        ></textarea>
      </div>
      
      <div class="modal-buttons">
        <button class="add-button" @click="addConclusion">Add Conclusion</button>
        <button class="close-button" @click="$emit('close')">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ConclusionsModal',
  props: {
    initialConclusions: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      conclusions: [
        { title: 'Satisfactory Performance', text: 'The employee demonstrated outstanding performance in their duties.' },
        { title: 'Good Collaboration', text: 'They excelled in their ability to work as part of a team.' },
        { title: 'Opportunity for Growth', text: 'Training in new technologies is recommended.' },
      ],
      newTitle: '',
      newText: '',
      editIndex: null
    };
  },
  methods: {
    addConclusion() {
      if (this.newTitle.trim() && this.newText.trim()) {
        this.conclusions.push({ title: this.newTitle.trim(), text: this.newText.trim() });
        this.resetFields();
      }
    },
    editConclusion(index) {
      this.newTitle = this.conclusions[index].title;
      this.newText = this.conclusions[index].text;
      this.editIndex = index;
    },
    deleteConclusion(index) {
      this.conclusions.splice(index, 1);
    },
    resetFields() {
      this.newTitle = '';
      this.newText = '';
    }
  }
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  width: 800px;
  max-height: 80%;
  overflow-y: auto;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.conclusions-list {
  margin-bottom: 15px;
}

.conclusion-item {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: #fff;
}

.item-actions {
  margin-top: 5px;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.new-conclusion {
  margin-top: 20px;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.add-button, .edit-button, .delete-button, .close-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.add-button:hover, .edit-button:hover {
  background: #0056b3;
}

.delete-button {
  background: #dc3545; /* Red */
}

.delete-button:hover {
  background: #c82333;
}

.close-button {
  background: #dc3545; /* Red */
}

.close-button:hover {
  background: #c82333;
}
</style>
