<template>
  <div class="former-employees">
    <h1>Former Employees</h1>
    <input type="text" v-model="searchQuery" placeholder="Search..." class="search-input" />
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Position</th>
          <th>Exit Date</th>
          <th>Documents</th>
          <th>Conclusions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="employee in filteredEmployees" :key="employee.id">
          <td>{{ employee.name }}</td>
          <td>{{ employee.position }}</td>
          <td>{{ employee.exitDate }}</td>
          <td>
            <button @click="openDocumentsModal(employee)" class="icon-button">
              <i class="fas fa-file-alt icon"></i>
            </button>
          </td>
          <td>
            <button @click="openConclusionsModal(employee.id)" class="icon-button">
              <i class="fas fa-comment icon"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Modales -->
    <DocumentModal v-if="isDocumentsModalVisible" :employeeName="selectedEmployee.name"
      :initialDocuments="selectedEmployee.documents" @close="isDocumentsModalVisible = false" />
    <ConclusionsModal v-if="isConclusionsModalVisible" @close="isConclusionsModalVisible = false" />

  </div>
</template>

<script>
import DocumentModal from './DocumentModal.vue';
import ConclusionsModal from './ConclusionsModal.vue';

export default {
  name: 'FormerEmployees',
  components: {
    DocumentModal,
    ConclusionsModal
  },
  data() {
    return {
      formerEmployees: [],
      searchQuery: '',
      isDocumentsModalVisible: false,
      isConclusionsModalVisible: false,
      selectedEmployee: {} // Cambiar a objeto para almacenar el empleado seleccionado
    };
  },
  computed: {
    filteredEmployees() {
      if (!this.searchQuery) {
        return this.formerEmployees;
      }
      const query = this.searchQuery.toLowerCase();
      return this.formerEmployees.filter(employee => {
        return (
          employee.name.toLowerCase().includes(query) ||
          employee.position.toLowerCase().includes(query) ||
          employee.exitDate.includes(query)
        );
      });
    }
  },
  methods: {
    fetchFormerEmployees() {
      // Simulación de empleados, puedes cambiar esto por tu lógica real
      this.formerEmployees = [
        { id: 1, name: 'Juan Pérez', position: 'Desarrollador', exitDate: '2023-01-15', documents: [] },
        { id: 2, name: 'Ana Gómez', position: 'Diseñadora', exitDate: '2022-12-30', documents: [] },
        { id: 3, name: 'Luis Rodríguez', position: 'Gerente', exitDate: '2023-02-10', documents: [] },
        { id: 4, name: 'Marta Fernández', position: 'Analista', exitDate: '2023-03-05', documents: [] },
        { id: 5, name: 'Carlos Sánchez', position: 'Marketing', exitDate: '2023-04-20', documents: [] },
        { id: 6, name: 'Patricia López', position: 'Recursos Humanos', exitDate: '2023-05-15', documents: [] },
        { id: 7, name: 'Javier Torres', position: 'Administrativo', exitDate: '2023-06-10', documents: [] },
        { id: 8, name: 'Sofía Martínez', position: 'Contadora', exitDate: '2023-07-25', documents: [] },
        { id: 9, name: 'Fernando Ruiz', position: 'Ingeniero de Sistemas', exitDate: '2023-08-30', documents: [] },
        { id: 10, name: 'Isabel Castro', position: 'Asistente Ejecutiva', exitDate: '2023-09-18', documents: [] }
      ];
    },
    openDocumentsModal(employee) {
      this.selectedEmployee = employee; // Almacena el empleado seleccionado
      this.isDocumentsModalVisible = true; // Abre el modal
    },
    openConclusionsModal(id) {
      this.selectedEmployeeId = id;
      this.isConclusionsModalVisible = true;
    }
  },
  mounted() {
    this.fetchFormerEmployees();
  }
};
</script>




<style scoped>
.former-employees {
  margin: 20px;
}

.search-input {
  margin-bottom: 10px;
  padding: 8px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
  text-align: left;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

button:hover {
  color: #007bff;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.icon {
  font-size: 24px;
  /* Cambia el tamaño del icono aquí */
  color: #3498db;
  /* Cambia el color aquí */
  transition: color 0.3s;
}

.icon-button:hover .icon {
