<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <h2>{{ employeeName }}'s Documents</h2>
      <p>You can view and manage the documents related to the employee.</p>

      <div class="search-container">
        <input type="text" v-model="searchQuery" placeholder="Search..." class="search-input" />
        <button @click="openUploadModal" class="add-button">Upload New Document</button>
      </div>

      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Document</th>
            <th>Creation Date</th>
            <th>Type</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(document, index) in filteredDocuments" :key="index">
            <td>{{ document.name }}</td>
            <td>{{ document.filename }}</td>
            <td>{{ document.creationDate }}</td>
            <td>{{ document.type }}</td>
            <td>
              <button @click="openDocument(document)" class="action-button view-button">
                <i class="fas fa-eye"></i> <!-- Eye icon for viewing -->
              </button>
              <button @click="deleteDocument(index)" class="action-button delete-button">
                <i class="fas fa-trash-alt"></i> <!-- Trash icon for deleting -->
              </button>
            </td>
          </tr>
        </tbody>
      </table>

      <button @click="$emit('close')" class="close-button" style="margin-left: auto;">Close</button>
    </div>

    <AddDocument v-if="isAddDocumentModalVisible" @close="isAddDocumentModalVisible = false" />
  </div>
</template>

<script>
import Swal from 'sweetalert2'; // Importar SweetAlert2
import AddDocument from './AddDocument.vue'; // Asegúrate de que la ruta sea correcta

export default {
  name: 'DocumentModal',
  components: {
    AddDocument, // Registra el componente
  },
  props: {
    employeeName: {
      type: String,
      required: true
    },
    initialDocuments: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      searchQuery: '',
      documents: this.initialDocuments,
      isAddDocumentModalVisible: false, // Controla la visibilidad del modal
    };
  },
  computed: {
    filteredDocuments() {
      if (!this.searchQuery) {
        return this.documents;
      }
      const query = this.searchQuery.toLowerCase();
      return this.documents.filter(doc => {
        return (
          doc.name.toLowerCase().includes(query) ||
          doc.filename.toLowerCase().includes(query) ||
          doc.creationDate.includes(query) ||
          doc.type.toLowerCase().includes(query)
        );
      });
    }
  },
  mounted() {
    this.generateRandomDocuments(); // Genera documentos aleatorios al montar
  },
  methods: {
    openDocument(document) {
      // Lógica para abrir el documento
      window.open(document.url, '_blank'); // Asegúrate de que el documento tenga una propiedad 'url'
    },
    deleteDocument(index) {
      Swal.fire({
        title: 'Are you sure?',
        text: 'Do you want to delete this document permanently?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#d33',
        cancelButtonColor: '#3085d6',
        confirmButtonText: 'Yes, delete it!',
        cancelButtonText: 'Cancel'
      }).then((result) => {
        if (result.isConfirmed) {
          this.documents.splice(index, 1); // Elimina el documento
          Swal.fire('Deleted!', 'Your document has been deleted.', 'success');
        }
      });
    },
    openUploadModal() {
      this.isAddDocumentModalVisible = true; // Cambia la visibilidad a true
    },
    generateRandomDocuments() {
      const types = ['Exams', 'Courses', 'Documents'];
      for (let i = 0; i < 10; i++) {
        const randomDocument = {
          name: `Document ${i + 1}`,
          filename: `document${i + 1}.pdf`,
          creationDate: new Date(Date.now() - Math.random() * 10000000000).toLocaleDateString(),
          type: types[Math.floor(Math.random() * types.length)],
          url: `https://example.com/document${i + 1}.pdf` // URL de ejemplo
        };
        this.documents.push(randomDocument);
      }
    }
  }
}
</script>


<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  width: 800px;
  max-height: 80%;
  overflow-y: auto;
}

.search-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.search-input {
  width: 100%;
  padding: 10px;
  margin-right: 10px; /* Space between input and button */
  border: 1px solid #ccc;
  border-radius: 5px;
}

.add-button {
  background-color: #28a745; /* Green */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.add-button:hover {
  background-color: #218838; /* Darker Green */
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 15px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

.action-button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  font-size: 20px; /* Adjust icon size */
  margin-right: 10px;
}

.view-button {
  color: #007bff; /* Blue color for view button */
}

.delete-button {
  color: #dc3545; /* Red color for delete button */
}

.close-button {
  background-color: #dc3545; /* Rojo */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: auto; /* Alinear a la derecha */
  display: block; /* Asegurarse de que el margen funcione */
}

.close-button:hover {
  background-color: #c82333; /* Darker Red */
}
</style>
